<template>
  <div class="container">


        <div class="row mt-5">
    <div class="card mx-auto">
      <div class="card-header text-white">
        <h4 class="fw-bold fs-2 mt-2 text-center">Student Register</h4>
      </div>
      <div class="card-body">
        <form @submit.prevent="registerUser">
          <div class="form-group">
            <label for="username" class="fw-strong fs-5 mt-1 text-white">Username</label>
            <input type="text" id="username" name="username" placeholder="Username"  v-model.trim="user.username"  class="form-control" :class="{ 'is-invalid': isSubmitted && $v.user.username.$error }" >
            <div v-if="isSubmitted && !$v.user.username.required" class="invalid-feedback">Username is required</div>
            <span v-if="!$v.user.username.minLength" class="invalid-feedback">username should be unique and at least 6 characters long</span>
          </div>
          <div class="form-group">
            <label for="name" class="fw-strong fs-5 mt-3 text-white">Name</label>
            <input type="text" id="name" name="name" placeholder="name" v-model="user.name" class="form-control" :class="{ 'is-invalid': isSubmitted && $v.user.name.$error }">
               <div v-if="isSubmitted && !$v.user.name.required" class="invalid-feedback">Name field is required</div>
          </div>
        <div class="form-group">
            <label for="email" class="fw-strong fs-5 mt-3 text-white">Email</label>
            <input type="text" id="email" name="email" placeholder="email"  v-model="user.email" class="form-control" :class="{ 'is-invalid': isSubmitted && $v.user.email.$error }" >
            <div v-if="isSubmitted && $v.user.email.$error" class="invalid-feedback">
                    <span v-if="!$v.user.email.required">Email field is required</span>
                    <span v-if="!$v.user.email.email">Please provide valid email</span>
                </div>
 
          </div>
        <div class="form-group">
            <label for="password" class="fw-strong fs-5 mt-3 text-white">Password</label>
            <input type="password" id="password" name="password" placeholder="Password" v-model="user.password" class="form-control" :class="{ 'is-invalid': isSubmitted && $v.user.password.$error }">
            <div v-if="isSubmitted && $v.user.password.$error" class="invalid-feedback">
                    <span v-if="!$v.user.password.required">Password field is required</span>
                    <span v-if="!$v.user.password.minLength">Password should be at least 6 characters long</span>
                </div>
          </div>
          <div class="form-group">
            <label for="confirm_password" class="fw-strong fs-5 mt-3 text-white">Confirm Password</label>
            <input type="password" id="confirm_password" name="confirm_password" placeholder="Confirm Password" v-model="user.confirm_password" class="form-control" :class="{ 'is-invalid': isSubmitted && $v.user.confirm_password.$error }">
            <div v-if="isSubmitted && $v.user.confirm_password.$error" class="invalid-feedback">
                    <span v-if="!$v.user.confirm_password.required">Confirm Password field is required</span>
                    <span v-else-if="!$v.user.confirm_password.sameAsPassword">Passwords should be matched</span>
                </div>
          </div>
          <!-- <div class="form-group">
            <label for="role" class="fw-strong fs-5 mt-3 text-white">Role</label>
            <input type="text" id="role" name="role" placeholder="Role" required v-model="user.role" class="form-control">
          </div> -->
          <div class="mt-3">
          <button class="btn btn-dark">Register</button><br>
          <span class="text-white mt-3">Already Have an account</span>
          <router-link to="/login" class="card-link ms-2 btn btn-primary">Login</router-link>
          </div>
        </form>
      </div>
    </div>
    </div>
</div>
</template>

<script>
import { mapActions } from "vuex";
    import { required, email, minLength, maxLength, sameAs } from "vuelidate/lib/validators";

export default {
name:'StudentRegister',
  data() {
    return {
      user:{
      username: "",
      name: "",
      email: "",
      password: "",
      confirm_password: "",
      role: "student",
      },
      isSubmitted: false
    };
  },
  validations:{
    user: {
      username:{ required, minLength: minLength(6), maxLength: maxLength(10) },
    name:{ required, },
    email:{ required, email },
    password:{ required, minLength: minLength(6)},
    confirm_password:{ required, sameAsPassword: sameAs('password')   },
  
  }
  },
  methods: {

    ...mapActions(["register"]),
    registerUser() {
          this.isSubmitted = true;
         this.$v.$touch();
                if (this.$v.$invalid) {
                    return;
                }
    
      let user = {
        username: this.user.username,
        name: this.user.name,
        email: this.user.email,
        password: this.user.password,
        confirm_password: this.user.confirm_password,
        role: this.user.role,
      };

// Original

      this.register(user).then((res) => {
        if (res.data.success) {
          this.$router.push("login");
 
this.$toasted.show(`Registered Succesfully`);
this.$toasted.show(`Login to Access dashboard`);
        }
        else{
this.$toasted.show(`${res.data.error}`);
        }
      });


    },
  
  
  }

}
</script>

<style scoped>

.card{
  width:50%;
  background:linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(23,74,84,1) 100%);
}

.form-control.is-invalid, .was-validated .form-control:invalid{

border-color:#dc3545;

}
.invalid-feedback{
  color:#dc3545
}

</style>